    <h4>首頁</h4>
    <div class="img-wrap">
        <img src="@/assets/images/work/pure_style_v1/final/pc_index.png" />
    </div>


    <h4>文章內頁</h4>
    <div class="img-wrap">
        <img src="@/assets/images/work/pure_style_v1/final/pc_post.png" />
    </div>


    <h4>選單</h4>
    <div class="img-wrap">
        <img src="@/assets/images/work/pure_style_v1/final/pc_fixed_menu.png" />
    </div>


    <h4>2022 新增 - 五星特別推薦區塊</h4>
    <div class="img-wrap">
        <img src="@/assets/images/work/pure_style_v1/final/pc_index_suntory.png" />
    </div>



    <h3>手機版</h3>

    <h4>首頁 & 文章內頁</h4>

    <div class="col-2-mobile-wrap">
        <div class="device-wrap mobile-dev-wrap" style="--device-w:375; --device-h:667;">
            <div class="dev-outter-wrap">
                <div class="dev-inner-wrap">
                    <img src="@/assets/images/work/pure_style_v1/final/mobile_index.png" />
                </div>
            </div>
        </div>
        <div class="device-wrap mobile-dev-wrap" style="--device-w:375; --device-h:667;">
            <div class="dev-outter-wrap">
                <div class="dev-inner-wrap">
                    <img src="@/assets/images/work/pure_style_v1/final/mobile_post.png" />
                </div>
            </div>
        </div>
    </div>

    <h4>選單 & 2022 新增 - 五星特別推薦區塊</h4>
    <div class="col-2-mobile-wrap">
        <div class="device-wrap mobile-dev-wrap" style="--device-w:375; --device-h:667;">
            <div class="dev-outter-wrap">
                <div class="dev-inner-wrap">
                    <img src="@/assets/images/work/pure_style_v1/final/mobile_menu.png" />
